import { useState, useRef } from "react"
import Popup from "../../../Components/popup"

const drugCodeOptions = [
  { id: 1, code: "PCM001", name: "Paracetamol" },
  { id: 2, code: "PCM002", name: "Paracetamol 500mg" },
  { id: 3, code: "IBU001", name: "Ibuprofen" },
  { id: 4, code: "ASP001", name: "Aspirin" },
  { id: 5, code: "DOL001", name: "Dolo" },
];

const OpeningBalanceEntry = () => {
  const [formData, setFormData] = useState({
    balanceEntryDate: "29/05/2025",
    enteredBy: "Rakesh",
    department: "DISPENSARY",
  })

  const [drugEntries, setDrugEntries] = useState([
    {
      id: 1,
      drugCode: "",
      drugName: "",
      unit: "",
      batchNoSerialNo: "",
      dom: "",
      doe: "",
      qty: "",
      unitRate: "",
      amount: "",
      unitsPerPack: "",
      purchaseRatePerUnit: "",
      gstPercent: "",
      mrpPerUnit: "",
      totalCost: "",
      brandName: "",
      manufacturer: "",
    },
  ])

  // Options for dropdowns
  const manufacturerOptions = [
    "Cipla Ltd",
    "Sun Pharma",
    "Dr. Reddy's",
    "Lupin Limited",
    "Aurobindo Pharma",
    "Torrent Pharma",
    "Glenmark",
    "Alkem Labs",
  ]

  const brandNameOptions = [
    "Paracetamol Plus",
    "Crocin",
    "Dolo",
    "Metacin",
    "Pyrigesic",
    "Aspirin",
    "Brufen",
    "Combiflam",
    "Saridon",
    "Disprin",
  ]

  const handleFormInputChange = (e) => {
    const { name, value } = e.target
    setFormData({
      ...formData,
      [name]: value,
    })
  }

  const [popupMessage, setPopupMessage] = useState(null)

  const handleDrugEntryChange = (index, field, value) => {
    const updatedEntries = drugEntries.map((entry, i) => {
      if (i === index) {
        const updatedEntry = { ...entry, [field]: value }

        // Auto-calculate amount when qty or unitRate changes
        if (field === "qty" || field === "unitRate") {
          const qty = Number.parseFloat(field === "qty" ? value : entry.qty) || 0
          const unitRate = Number.parseFloat(field === "unitRate" ? value : entry.unitRate) || 0
          updatedEntry.amount = (qty * unitRate).toFixed(2)
        }

        // Auto-calculate totalCost when qty, purchaseRatePerUnit, or gstPercent changes
        if (field === "qty" || field === "purchaseRatePerUnit" || field === "gstPercent") {
          const qty = Number.parseFloat(field === "qty" ? value : entry.qty) || 0
          const purchaseRate =
            Number.parseFloat(field === "purchaseRatePerUnit" ? value : entry.purchaseRatePerUnit) || 0
          const gstPercent = Number.parseFloat(field === "gstPercent" ? value : entry.gstPercent) || 0

          const baseAmount = qty * purchaseRate
          const gstAmount = (baseAmount * gstPercent) / 100
          updatedEntry.totalCost = (baseAmount + gstAmount).toFixed(2)
        }

        return updatedEntry
      }
      return entry
    })
    setDrugEntries(updatedEntries)
  }

  const addNewRow = () => {
    const newEntry = {
      id: Date.now(), // Use timestamp for unique ID
      drugCode: "",
      drugName: "",
      unit: "",
      batchNoSerialNo: "",
      dom: "",
      doe: "",
      qty: "",
      unitRate: "",
      amount: "",
      unitsPerPack: "",
      purchaseRatePerUnit: "",
      gstPercent: "",
      mrpPerUnit: "",
      totalCost: "",
      brandName: "",
      manufacturer: "",
    }
    setDrugEntries([...drugEntries, newEntry])
  }

  const removeRow = (index) => {
    if (drugEntries.length > 1) {
      const filteredEntries = drugEntries.filter((_, i) => i !== index)
      setDrugEntries(filteredEntries)
    }
  }

  const handleSubmit = () => {
    // Validate required fields
    const hasEmptyRequiredFields = drugEntries.some(
      (entry) => !entry.drugCode || !entry.drugName || !entry.qty || !entry.unitRate,
    )

    if (hasEmptyRequiredFields) {
      showPopup("Please fill in all required fields (Drug Code, Drug Name, Qty)", "error")
      return
    }

    const submissionData = {
      ...formData,
      drugEntries: drugEntries.filter((entry) => entry.drugCode || entry.drugName),
    }

    console.log("Submitting:", submissionData)
    showPopup(" Items updated successfully!", "success")
  }

  const showPopup = (message, type = "info") => {
    setPopupMessage({
      message,
      type,
      onClose: () => {
        setPopupMessage(null)
      },
    })
  }

  const handleReset = () => {
    setFormData({
      balanceEntryDate: "29/05/2025",
      enteredBy: "sumit",
      department: "DISPENSARY",
    })
    setDrugEntries([
      {
        id: 1,
        drugCode: "",
        drugName: "",
        unit: "",
        batchNoSerialNo: "",
        dom: "",
        doe: "",
        qty: "",
        unitRate: "",
        amount: "",
        unitsPerPack: "",
        purchaseRatePerUnit: "",
        gstPercent: "",
        mrpPerUnit: "",
        totalCost: "",
        brandName: "",
        manufacturer: "",
      },
    ])
  }

  const dropdownClickedRef = useRef(false);
  const [activeDrugCodeDropdown, setActiveDrugCodeDropdown] = useState(null);

  return (
    <div className="content-wrapper">
      <div className="row">
        <div className="col-12 grid-margin stretch-card">
          <div className="card form-card">
            <div className="card-header">
              <h4 className="card-title p-2 mb-0">Opening Balance Entry</h4>
            </div>

            <div className="card-body">
              {/* Entry Details Section */}
              <div className="mb-4">
                <h5 className=" mb-3">Entry Details:</h5>
                <div className="row g-3">
                  <div className="col-md-3">
                    <label className="form-label fw-bold">Balance Entry Date</label>
                    <input
                      type="text"
                      className="form-control"
                      name="balanceEntryDate"
                      value={formData.balanceEntryDate}
                      onChange={handleFormInputChange}
                      style={{ backgroundColor: "#f8f9fa" }}
                      readOnly
                    />
                  </div>
                  <div className="col-md-3">
                    <label className="form-label fw-bold">Entered By</label>
                    <input
                      type="text"
                      className="form-control"
                      name="enteredBy"
                      value={formData.enteredBy}
                      onChange={handleFormInputChange}
                      style={{ backgroundColor: "#f8f9fa" }}
                      readOnly
                    />
                  </div>
                  <div className="col-md-3">
                    <label className="form-label fw-bold">Department</label>
                    <input
                      type="text"
                      className="form-control"
                      name="department"
                      value={formData.department}
                      onChange={handleFormInputChange}
                      style={{ backgroundColor: "#f8f9fa" }}
                      readOnly
                    />
                  </div>
                </div>
              </div>

              {/* Drug Entry Table - Horizontally Scrollable */}
              <div className="table-responsive" style={{ overflowX: "auto", maxWidth: "100%" }}>
                <table className="table table-bordered table-hover align-middle" style={{ minWidth: "2200px" }}>
                  <thead style={{ backgroundColor: "#6c7b7f", color: "white" }}>
                    <tr>
                      <th className="text-center" style={{ width: "60px", minWidth: "60px" }}>
                        S.No.
                      </th>
                      <th style={{ width: "120px", minWidth: "120px" }}>Drug Code</th>
                      <th style={{ width: "200px", minWidth: "200px" }}>Drug Name</th>
                      <th style={{ width: "80px", minWidth: "80px" }}>Unit</th>
                      <th style={{ width: "150px", minWidth: "150px" }}>Batch No/ Serial No</th>
                      <th style={{ width: "120px", minWidth: "120px" }}>DOM</th>
                      <th style={{ width: "120px", minWidth: "120px" }}>DOE</th>
                      <th style={{ width: "80px", minWidth: "80px" }}>Qty</th>
                      <th style={{ width: "100px", minWidth: "100px" }}>Units Per Pack</th>
                      <th style={{ width: "120px", minWidth: "120px" }}>Purchase Rate/Unit</th>
                      <th style={{ width: "100px", minWidth: "100px" }}>GST Percent</th>
                      <th style={{ width: "100px", minWidth: "100px" }}>MRP/Unit</th>
                      <th style={{ width: "100px", minWidth: "100px" }}>Total Cost</th>
                      <th style={{ width: "150px", minWidth: "150px" }}>Brand Name</th>
                      <th style={{ width: "150px", minWidth: "150px" }}>Manufacturer</th>
                      <th style={{ width: "60px", minWidth: "60px" }}>Add</th>
                      <th style={{ width: "70px", minWidth: "70px" }}>Delete</th>
                    </tr>
                  </thead>
                  <tbody>
                    {drugEntries.map((entry, index) => (
                      <tr key={entry.id}>
                        <td className="text-center fw-bold">{index + 1}</td>
                        <td style={{ position: "relative" }}>
                          <input
                            type="text"
                            className="form-control form-control-sm"
                            value={entry.drugCode}
                            onChange={(e) => {
                              const value = e.target.value;
                              handleDrugEntryChange(index, "drugCode", value);
                              if (value.length > 0) {
                                setActiveDrugCodeDropdown(index);
                              } else {
                                setActiveDrugCodeDropdown(null);
                              }
                            }}
                            placeholder="Code"
                            style={{ minWidth: "100px" }}
                            autoComplete="off"
                            onFocus={() => setActiveDrugCodeDropdown(index)}
                            onBlur={() => {
                              setTimeout(() => {
                                if (!dropdownClickedRef.current) {
                                  setActiveDrugCodeDropdown(null);
                                }
                                dropdownClickedRef.current = false;
                              }, 150);
                            }}
                          />
                          {activeDrugCodeDropdown === index && (
                            <ul className="list-group position-absolute w-100 mt-1" style={{ zIndex: 1000, maxHeight: 180, overflowY: 'auto' }}>
                              {drugCodeOptions
                                .filter((opt) => 
                                  entry.drugCode === "" || 
                                  opt.code.toLowerCase().includes(entry.drugCode.toLowerCase()) ||
                                  opt.name.toLowerCase().includes(entry.drugCode.toLowerCase())
                                )
                                .map((opt) => (
                                  <li
                                    key={opt.id}
                                    className="list-group-item list-group-item-action"
                                    style={{ cursor: 'pointer' }}
                                    onMouseDown={(e) => {
                                      e.preventDefault();
                                      dropdownClickedRef.current = true;
                                    }}
                                    onClick={(e) => {
                                      e.preventDefault();
                                      e.stopPropagation();
                                      
                                      // Update both drug code and drug name
                                      const updatedEntries = drugEntries.map((entry, i) => {
                                        if (i === index) {
                                          return {
                                            ...entry,
                                            drugCode: opt.code,
                                            drugName: opt.name
                                          };
                                        }
                                        return entry;
                                      });
                                      
                                      setDrugEntries(updatedEntries);
                                      setActiveDrugCodeDropdown(null);
                                      dropdownClickedRef.current = false;
                                    }}
                                  >
                                    {opt.code} - {opt.name}
                                  </li>
                                ))}
                              {drugCodeOptions.filter((opt) => 
                                entry.drugCode === "" || 
                                opt.code.toLowerCase().includes(entry.drugCode.toLowerCase()) ||
                                opt.name.toLowerCase().includes(entry.drugCode.toLowerCase())
                              ).length === 0 && entry.drugCode !== "" && (
                                <li className="list-group-item text-muted">No matches found</li>
                              )}
                            </ul>
                          )}
                        </td>
                        <td>
                          <input
                            type="text"
                            className="form-control form-control-sm"
                            value={entry.drugName}
                            onChange={(e) => handleDrugEntryChange(index, "drugName", e.target.value)}
                            placeholder="Drug Name"
                            style={{ minWidth: "180px" }}
                          />
                        </td>
                        <td>
                          <input
                            type="text"
                            className="form-control form-control-sm"
                            value={entry.unit}
                            onChange={(e) => handleDrugEntryChange(index, "unit", e.target.value)}
                            placeholder="Unit"
                            style={{ minWidth: "70px" }}
                          />
                        </td>
                        <td>
                          <input
                            type="text"
                            className="form-control form-control-sm"
                            value={entry.batchNoSerialNo}
                            onChange={(e) => handleDrugEntryChange(index, "batchNoSerialNo", e.target.value)}
                            placeholder="Batch/Serial"
                            style={{ minWidth: "130px" }}
                          />
                        </td>
                        <td>
                          <input
                            type="date"
                            className="form-control form-control-sm"
                            value={entry.dom}
                            onChange={(e) => handleDrugEntryChange(index, "dom", e.target.value)}
                            style={{ minWidth: "120px" }}
                          />
                        </td>
                        <td>
                          <input
                            type="date"
                            className="form-control form-control-sm"
                            value={entry.doe}
                            onChange={(e) => handleDrugEntryChange(index, "doe", e.target.value)}
                            style={{ minWidth: "120px" }}
                          />
                        </td>
                        <td>
                          <input
                            type="number"
                            className="form-control form-control-sm"
                            value={entry.qty}
                            onChange={(e) => handleDrugEntryChange(index, "qty", e.target.value)}
                            placeholder="0"
                            min="0"
                            step="0.01"
                            style={{ minWidth: "70px" }}
                          />
                        </td>
                      
                        <td>
                          <input
                            type="number"
                            className="form-control form-control-sm"
                            value={entry.unitsPerPack}
                            onChange={(e) => handleDrugEntryChange(index, "unitsPerPack", e.target.value)}
                            placeholder="0"
                            min="0"
                            step="1"
                            style={{ minWidth: "90px" }}
                          />
                        </td>
                        <td>
                          <input
                            type="number"
                            className="form-control form-control-sm"
                            value={entry.purchaseRatePerUnit}
                            onChange={(e) => handleDrugEntryChange(index, "purchaseRatePerUnit", e.target.value)}
                            placeholder="0.00"
                            min="0"
                            step="0.01"
                            style={{ minWidth: "110px" }}
                          />
                        </td>
                        <td>
                          <input
                            type="number"
                            className="form-control form-control-sm"
                            value={entry.gstPercent}
                            onChange={(e) => handleDrugEntryChange(index, "gstPercent", e.target.value)}
                            placeholder="0"
                            min="0"
                            max="100"
                            step="0.01"
                            style={{ minWidth: "90px" }}
                          />
                        </td>
                        <td>
                          <input
                            type="number"
                            className="form-control form-control-sm"
                            value={entry.mrpPerUnit}
                            onChange={(e) => handleDrugEntryChange(index, "mrpPerUnit", e.target.value)}
                            placeholder="0.00"
                            min="0"
                            step="0.01"
                            style={{ minWidth: "90px" }}
                          />
                        </td>
                        <td>
                          <input
                            type="text"
                            className="form-control form-control-sm"
                            value={entry.totalCost}
                            readOnly
                            style={{ backgroundColor: "#f8f9fa", minWidth: "90px" }}
                          />
                        </td>
                        <td>
                          <select
                            className="form-select form-select-sm"
                            value={entry.brandName}
                            onChange={(e) => handleDrugEntryChange(index, "brandName", e.target.value)}
                            style={{ minWidth: "130px" }}
                          >
                            <option value="">Select Brand</option>
                            {brandNameOptions.map((option, idx) => (
                              <option key={idx} value={option}>
                                {option}
                              </option>
                            ))}
                          </select>
                        </td>
                      
                        <td>
                          <select
                            className="form-select form-select-sm"
                            value={entry.manufacturer}
                            onChange={(e) => handleDrugEntryChange(index, "manufacturer", e.target.value)}
                            style={{ minWidth: "130px" }}
                          >
                            <option value="">Select</option>
                            {manufacturerOptions.map((option, idx) => (
                              <option key={idx} value={option}>
                                {option}
                              </option>
                            ))}
                          </select>
                        </td>
                        <td className="text-center">
                          <button
                            type="button"
                            className="btn btn-success"
                            onClick={addNewRow}
                            style={{
                              color: "white",
                              border: "none",
                              width: "35px",
                              height: "35px",
                            }}
                            title="Add Row"
                          >
                            +
                          </button>
                        </td>
                        <td className="text-center">
                          <button
                            type="button"
                            className="btn btn-danger btn-sm"
                            onClick={() => removeRow(index)}
                            disabled={drugEntries.length === 1}
                            title="Delete Row"
                            style={{
                              width: "35px",
                              height: "35px",
                            }}
                          >
                            -
                          </button>
                        </td>
                      </tr>
                    ))}
                  </tbody>
                </table>
              </div>

              {popupMessage && (
                <Popup message={popupMessage.message} type={popupMessage.type} onClose={popupMessage.onClose} />
              )}

              {/* Action Buttons */}
              <div className="d-flex justify-content-end gap-2 mt-4">
                 <button type="button" className="btn btn-warning">
                  Save
                </button>
                <button type="button" className="btn btn-success" onClick={handleSubmit}>
                  Submit
                </button>
                <button type="button" className="btn btn-danger" onClick={handleReset}>
                  Reset
                </button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  )
}

export default OpeningBalanceEntry